body {
    background: radial-gradient(white 3px, transparent 4px),
      radial-gradient(white 3px, transparent 4px),
      linear-gradient(#fff 4px, transparent 0),
      linear-gradient(
        45deg,
        transparent 74px,
        transparent 75px,
        #a4a4a4 75px,
        #a4a4a4 76px,
        transparent 77px,
        transparent 109px
      ),
      linear-gradient(
        -45deg,
        transparent 75px,
        transparent 76px,
        #a4a4a4 76px,
        #a4a4a4 77px,
        transparent 78px,
        transparent 109px
      ),
      #fff;

    background-size: 109px 109px, 109px 109px, 100% 6px, 109px 109px, 109px 109px;
    background-position: 54px 55px, 0px 0px, 0px 0px, 0px 0px, 0px 0px;
  }
  

#TriviaDiv{
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: DesdeIzquierda;
            animation-name: DesdeIzquierda;
}
#footer {
    position: relative;
    bottom:0;
 
} 
#MainDiv{
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: DesdeArriba;
            animation-name: DesdeArriba;
}
@-webkit-keyframes DesdeIzquierda {
    from {
      margin-left: 100%;
      width: 250%
    }
  
    to {
      margin-left: 0%;
      width: 100%;
    }
  }
@keyframes DesdeIzquierda {
    from {
      margin-left: 100%;
      width: 250%
    }
  
    to {
      margin-left: 0%;
      width: 100%;
    }
  }


  @-webkit-keyframes DesdeArriba {
    from {
      margin-top: 100%;
      height:0%;
    }
  
    to {
        margin-top: 0%;
        height:100%;
    }
  }


  @keyframes DesdeArriba {
    from {
      margin-top: 100%;
      height:0%;
    }
  
    to {
        margin-top: 0%;
        height:100%;
    }
  }
